<template>
    <div style="width:100%;height:100%;display: flex;justify-content: center;">
        <div style="width:90%;padding:30px;">
            <el-table
                :pagination="true"
                :data="checkTableData"
                border
                style="width: 100%">
                <el-table-column
                type="index"
                label="序号"
                width="100">
                <template slot-scope="scope">
                  <span>{{(currentPage - 1) * pageSize + scope.$index + 1}}</span>
                </template>
                </el-table-column>
                <el-table-column
                prop="resultName"
                label="成果名称"
                width="200">
                </el-table-column>
                <el-table-column
                prop="level"
                label="成果级别"
                width="150">
                <template v-slot:default="scope">
                  {{  scope.row.resultDetails && scope.row.resultDetails.level ? scope.row.resultDetails.level : '未上传'  }}
                </template>
                </el-table-column>
                <el-table-column 
                label="成果类型"
                width="150">
                <template v-slot:default="scope">
                  {{  scope.row.resultDetails && scope.row.resultDetails.type ? scope.row.resultDetails.type : '未上传'  }}
                </template>
                </el-table-column>
                <el-table-column
                prop="contextType"
                label="数据格式"
                width="150">
                </el-table-column>
                <el-table-column
                label="提交人"
                width="150">
                <template v-slot:default="scope">
                  {{  scope.row.resultDetails && scope.row.resultDetails.personName ? scope.row.resultDetails.personName : '无'  }}
                </template>
                </el-table-column>
                <el-table-column
                label="提交时间"
                width="200">
                <template v-slot:default="scope">
                  {{  scope.row.resultDetails && scope.row.resultDetails.submitTime ? scope.row.resultDetails.submitTime : '无'  }}
                </template>
                </el-table-column>
                <el-table-column
                label="审核状态"
                width="150"
                >
                <template v-slot:default="scope">
                  <el-tag
                    :type="scope.row.resultStatus === 1 ? 'warning' : 'success'"
                    disable-transitions>{{scope.row.resultStatus === 1 ? '未审核' : ((scope.row.resultStatus === 2 ? '审核通过':'审核未通过'))}}</el-tag>
                </template>
                </el-table-column>
                <el-table-column
                fixed="right"
                label="审核操作">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.resultStatus === 1" @click="handleClick(scope.row.resultId)" type="text" size="small">进入审核</el-button>
                    <el-button v-if="scope.row.resultStatus === 2" @click="handleClick(scope.row.resultId)" type="text" size="small">信息查看</el-button>
                    <el-button v-if="scope.row.resultStatus === 3" @click="handleClick(scope.row.resultId)" type="text" size="small">信息查看</el-button>
                </template>
                </el-table-column>
            </el-table>
            <div class="block">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-sizes="[5, 10, 20, 50, 100]"
                :page-size="this.pageSize"
                layout="sizes, prev, pager, next"
                :total="this.pageTotal">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
import myaxios from '../../utils/httpRequest'
export default {
   data() {
      return {
        checkTableData: [],
        // 分页
        currentPage:1,  //当前页
        pageSize:10,
        pageTotal:0,
      }
   },
   created(){
   },
   computed:{
   },
   methods:{
    //获取表行跳转详情
    handleClick(resultId) {
        console.log(resultId);
        this.$router.push(`/checkDetail/${resultId}`)
    },
    //分页
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.pageSize = val;
        this.findGeoResultPage();
        
    },
    handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currentPage = val;
        this.findGeoResultPage();
    },
    findGeoResultPage(){
      myaxios.get('/findGeoResultPage',{
        params:{
          pageNo:this.currentPage,
          pageSize:this.pageSize,
        }
      }).then(res => {
        console.log(res);
        this.pageTotal = res.data.data.total;
        this.checkTableData = res.data.data.rows;
        //按审核与否排序
        this.checkTableData.sort((a, b) => {
          if (a.resultStatus === 1 && b.resultStatus !== 1) {
            return -1; // a 排在前面
          } else if (a.resultStatus === 3 && b.resultStatus === 2) {
            return -1; // a 排在前面
          } else if (a.resultStatus === 2 && b.resultStatus !== 2) {
            return 1; // b 排在前面
          } else {
            return 0; // 保持原顺序
          }
        });

        console.log(this.checkTableData)
      }).catch(err => {
        console.log(err)
      })
    },
   },
   mounted(){
    this.findGeoResultPage()
   }
}
</script>
<style lang="scss" scoped>
.block {
    display: flex;
    justify-content: right;
    align-items: center;
    width: 100%;
    height: 60px;
}
</style>
