                            </el-input>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <el-tabs type="card" stretch style="width:90%">
                <el-tab-pane label="全国级成果">
                    <el-table
                        :default-sort = "{prop: 'date', order: 'descending'}"
                        :data="countryTableData.filter(data => !search || data.resultName.toLowerCase().includes(search.toLowerCase()))"
                        height="500"
                        border
                        style="width: 100%">
                        <el-table-column
                        label="序号"
                        type="index"
                        width="150">
                        </el-table-column>
                        <el-table-column
                        sortable
                        prop="resultName"
                        label="成果名称"
                        :width="{ width: '20%' }">
                        </el-table-column>
                        <el-table-column
                        label="成果类型"
                        :width="{ width: '20%' }">
                        <template v-slot:default="scope">
                            {{  scope.row.resultDetails.type  }}
                        </template>
                        </el-table-column>
                        <el-table-column
                        prop="contextType"
                        label="数据格式"
                        :width="{ width: '20%' }">
                        </el-table-column>
                        <el-table-column
                        sortable
                        prop="date"
                        label="更新时间"
                        :width="{ width: '20%' }">
                        <template v-slot:default="scope">
                            {{  scope.row.resultDetails && scope.row.resultDetails.submitTime ? scope.row.resultDetails.submitTime : '无'  }}
                        </template>
                        </el-table-column>
                        <el-table-column
                        label="成果浏览">
                            <template v-slot:default="scope">
