                            <template v-slot:default="scope">
                                 <el-link type="success" @click="toResultDetail(scope.row.resultId)">查看成果</el-link>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="项目级成果">
                    <el-table
                        :default-sort = "{prop: 'date', order: 'descending'}"
                        :data="demoTableData.filter(data => !search || data.resultName.toLowerCase().includes(search.toLowerCase()))"
                        height="500"
                        border
