</template>
<script>
import myaxios from '../../utils/httpRequest';
import GeoResultMana from '@/api/GeoResultMana';
export default {
   data() {
      return {
        // 搜索
        select: '',
        search:'',
        countryTableData:[],
        regionTableData:[],
        demoTableData:[],
        dotTableData:[],
      }
   },
   created(){
   },
   mounted(){
    this.getAllGeoResults();
   },
   computed:{
   },
   methods:{
    toMapDialog(){
        this.$router.push('/catalog')
    },
    toResultDetail(id){
        console.log(id);
        this.$router.push(`/typeTableDetail/${id}`)
    },
    async getAllGeoResults(){
        let res = await GeoResultMana.findGeoResultList(null,2);
        for(let i = 0; i<res.data.data.length;i++){
            if(res.data.data[i].resultDetails.level === '工点级'){
                this.dotTableData.push(res.data.data[i])
            }
            else if(res.data.data[i].resultDetails.level === '项目级'){
                this.demoTableData.push(res.data.data[i])
            }
            else if(res.data.data[i].resultDetails.level === '区域级'){
                this.regionTableData.push(res.data.data[i])
            }else{
                this.countryTableData.push(res.data.data[i])
            }
        }
        console.log("工点",this.dotTableData,"项目",this.demoTableData,"区域",this.regionTableData,"全国",this.countryTableData);
    }
   },
}
</script>
<style lang="scss" scoped>
:deep(.el-tabs--card>.el-tabs__header .el-tabs__item){
  font-weight: 900 !important;
}
</style>
